.question {
  padding-top: 50px;
  @include r(767) {
    padding-top: 35px; }
  h1 {
    text-align: left;
    margin-bottom: 50px;
    @include r(767) {
      text-align: center;
      margin-bottom: 35px; } }
  .btn {
    background-color: $pink;
    box-shadow: 0 12px 20px 0 rgba(255, 142, 197, 0.1);
    display: inline-flex;
    width: 255px;
    &:hover {
      box-shadow: 0px 10px 50px rgba($pink, 0.7); }
    @include r(767) {
      width: 100%;
      margin-bottom: 35px; } }
  &__content {
    h2 {
      font-family: 'DINRoundPro-Medi';
      font-size: 24px;
      color: $black;
      margin-bottom: 12px; }
    span {
      font-size: 12px;
      color: $black;
      margin-bottom: 35px;
      display: inline-block; }
    p {
      font-size: 18px;
      line-height: 1.56;
      color: $black;
      margin-bottom: 50px; }
    b {
      font-family: 'DINRoundPro-Medi';
      font-size: 18px;
      display: flex;
      justify-content: flex-end;
      color: $black; } }
  &__client {
    padding: 30px;
    border-radius: 30px;
    background-color: rgba(0, 177, 255, 0.15);
    margin-bottom: 25px;
    position: relative;
    @include r(767) {
      margin-bottom: 15px; } }
  &__clinic {
    padding: 30px;
    border-radius: 30px;
    background-color: #e8fff4;
    margin-bottom: 50px;
    position: relative;
    &:before {
      content: '';
      width: 65px;
      height: 65px;
      position: absolute;
      top: 0;
      left: -95px;
      background: url('../img/r-2.png') no-repeat;
      background-size: cover;
      @include r(767) {
        display: none; } }
    @include r(767) {
      margin-bottom: 35px; }
    &__title {
      @include center-y;
      margin-bottom: 20px;
      &__img {
        width: 80px;
        height: 80px;
        overflow: hidden;
        border-radius: 100%;
        margin-right: 15px;
        img {
          width: 100%; } }
      &__text {
        span {
          margin-bottom: 0;
          i {
            display: block;
            font-style: normal; } } } } }
  &__video {
    margin-top: 50px;
    background-color: #f8f8f8;
    padding: 50px 0;
    @include r(767) {
      padding: 35px 0;
      margin-top: 0; }
    h2 {
      font-size: 24px;
      font-weight: bold;
      text-align: center;
      color: $bright-sky-blue-four;
      margin-bottom: 50px;
      @include r(767) {
        margin-bottom: 35px; } }
    .swiper-slide {
      overflow: hidden;
      border-radius: 30px;
      @include center;
      position: relative;
      height: 205px;
      @include r(767) {
        height: 145px; }
      img {
        width: 100%;
        height: 100%;
        @include object-fit(cover); }
      a {
        position: absolute;
        width: 50px;
        height: 50px;
        @include center;
        border-radius: 100%;
        background: $bright-sky-blue-two;
        font-size: 40px;
        color: $white; } } } }
