.home-shares {
  background-color: $light-pink;
  padding: 50px 0;
  @include r(767) {
    padding: 30px 0; }
  .btn {
    @include r(767) {
      width: 100%; } }
  .swiper {
    &-container {
      margin-bottom: 40px;
      @include r(767) {
        padding-bottom: 45px;
        margin-bottom: 0; } }
    &-pagination {
      @include r(767) {
        bottom: 15px; } }
    &-slide {
      height: 270px;
      border-radius: 30px;
      @include center-column;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50% 50%;
      &:before {
        content: '';
        @include coverdiv;
        background-image: linear-gradient(to right, rgba(0, 177, 255, 0.47), rgba(255, 150, 195, 0.94));
        border-radius: 30px; }
      @include r(767) {
        height: 145px; }
      .home-shares__block {
        position: relative;
        width: 100%;
        height: 100%;
        @include center-column;
        &:before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          border-radius: 30px;
          background-image: linear-gradient(to right, rgba(0, 177, 255, 0.47), rgba(255, 150, 195, 0.94));
          z-index: -1; } }
      h4 {
        font-size: 24px;
        font-weight: bold;
        text-align: center;
        color: $white;
        @include r(767) {
          padding: 0 15px; } }
      p {
        font-size: 18px;
        font-weight: normal;
        color: $white;
        @include r(767) {
          display: none; } } } } }
